<template>
  <div class="professional1 padding students">
    <!-- 专业管理 -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/main/professional' }">基础信息</el-breadcrumb-item>
        <el-breadcrumb-item>
            专业管理
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
     <div class="examination-dv">
        <img src="@/assets/mao.png" alt="">
        <span>专业管理</span>
    </div>
    <div class="professional-inp">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <span class="ssssss">所属学院：</span>
        <el-select v-model="value0" placeholder="全部" class="pppp" @change="changes1">
          <el-option
            v-for="item in options0"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        <el-form-item label="搜索：">
          <el-input v-model="formInline.user" placeholder="专业名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit" class="btn">搜索</el-button>
        </el-form-item>
      </el-form>
      <div class="professional-btn" >
        <el-button type="primary" round @click="close">+ 新建</el-button>
      </div>
    </div>

    
      <!-- 弹框组件 -->
    <el-dialog :title="Addpro" :visible.sync="dialogFormVisible" :before-close="handleClose" :close-on-click-modal='false'>
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="所属学院：" :label-width="formLabelWidth" >
          <el-select v-model="value" placeholder="" >
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        </el-form-item>
        
        <el-form-item label="专业名称：" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" auto-complete="off"></el-input> 
        </el-form-item>
        <el-form-item label="专业编码：" :label-width="formLabelWidth" prop="code">
          <el-input v-model="form.code" auto-complete="off"></el-input> 
        </el-form-item>
         <el-form-item label="专业简介：" :label-width="formLabelWidth" prop="intro">
          <el-input v-model="form.intro" auto-complete="off"></el-input> 
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="ppp">取 消</el-button>
        <el-button type="primary" @click="seccons1('form')" v-show="falg">确 定</el-button>
        <el-button type="primary" @click="seccons2('form')" v-show="falg1">确 定</el-button>

      </div>
    </el-dialog>

    <div class="professional-tab">
      <template>
        <el-table :data="tableData" border stripe style="width: 100%" v-loading="loading">
          <el-table-column label="专业编码" width="180">
            <template slot-scope="scope">
              <span>{{ scope.row.code }}</span>
            </template>
          </el-table-column>
          <el-table-column label="专业名称" width="220">
            <template slot-scope="scope">
              <span>{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="所属院校" width="180">
            <template slot-scope="scope">
              <span>{{ scope.row.academyName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="专业简介" >
            <template slot-scope="scope">
              <span
                style="overflow: hidden;white-space: nowrap;text-overflow: ellipsis;"
              >{{ scope.row.intro }}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="180">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">
                <i class="el-icon-edit"></i>&nbsp;&nbsp;编辑
              </el-button>
              <el-button type="text" size="small" @click="handleDelete(scope.$index, scope.row)">
                <i class="el-icon-delete"></i>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <div>
      <template>
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="pageSize"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dialogFormVisible: false,
      formLabelWidth: '120px',
      falg:false,
      falg1:false,
      Addpro:'',
       form: {
          name: "",
          code: "",
          intro: "",
          academyId: ""
        },
      currentPage4: 1,
      total:0,
      page: 1,
      pageSize: 15,
      pageNum:1,
      formInline: {
        user: "",
      },
      rules:{
        name: [{required: true, message: '请输入专业名称', trigger: 'blur'}],
        code: [{required: true, message: '请输入专业编号', trigger: 'blur'}],
        intro: [{required: true, message: '请输入专业简介', trigger: 'blur'}],
      },
      tableData: [],
      loading: true,
      options:[],
      value: '',
      options0:[],
      value0: '',
      options1:[],
      value1: '',
    };
  },
  methods: {
    onSubmit() {
      let keywords = {
        academyId : this.value0,
        keywords : this.formInline.user,
        page:this.pageNum,
        rows:this.pageSize
      }
      this.$post('/basic/specialty/list',keywords).then(res=>{
        if(res.status == 1){
          this.tableData = res.data
          this.total = res.total
          this.loading = false
        }
      })
    },
    ppp(form){
      this.$refs['form'].resetFields();
        this.dialogFormVisible = false
      this.onSubmit()
    },
    // /basic/academy/all 所有学院
    allxue(){
      this.$get(`/basic/academy/all?type=${1}`).then(res=>{
        this.options = JSON.parse(JSON.stringify(res.data))
          this.options0 = res.data
          this.options0.unshift({code:'',name:'全部'})
          
      })
    },
    //  跟据院校查专业
    changes1(){
      this.$get(`/basic/specialty/all/${this.value0}`).then(res=>{
          this.options1 = res.data
      })
    },
    //  新增专业     /basic/specialty/add
    specialtyadd(){
      let form = {
          name:this.form.name,
          code:this.form.code,
          intro:this.form.intro,
          academyId:this.value
      }
      this.$post('/basic/specialty/add',form).then(res=>{
          if(res.status == 1){
            this.$notify({
                message: '新增成功',
                type: 'success'
            });
         
            this.tableData.unshift(res.data)
            this.onSubmit()
          }
      })
    },
    handleEdit(index, row) {
      //编辑对话框显示值
      this.Addpro = '编辑专业'
      this.falg = false
      this.falg1 = true
      this.dialogFormVisible = true
      this.form = row
      this.value = row.academyId
      let id = this.form.id
      this.allxue()
    },
    seccons1(form){
      // 确定按钮的点击提交编辑内容
      this.$refs[form].validate((valid) => {
          if (valid) {
            this.specialtyadd()
            this.dialogFormVisible = false
            this.form = {}
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      
    },
    seccons2(){
      // 确定按钮的点击提交编辑内容 /basic/specialty/edit
      let form ={
          code:this.form.code,
          name:this.form.name,
          id:this.form.id,
          intro:this.form.intro,
          academyId:this.value
      }
      this.$put('/basic/specialty/edit',form).then(res=>{
        this.form = res.data
        this.onSubmit()
      })
      this.dialogFormVisible = false
    },
    close(){
      this.Addpro = '添加专业'
      this.falg = true
      this.falg1 = false
      this.dialogFormVisible = true
      this.form = {}
      this.allxue()
    },
    handleClose(){
       this.$refs['form'].resetFields();
      this.dialogFormVisible = false
      this.onSubmit()
    },
    handleDelete(index, row) {
      this.$confirm('确定删除该信息?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            this.$deletes(`/basic/specialty/del/${row.id}`).then(res=>{
              if(res.status == 1){
                this.onSubmit()
              }
            })
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        });
    },
    indexMethod(index) {
      return index * 1;
    },
    handleSizeChange(val) {
     this.pageSize = val;
     this.onSubmit()
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.onSubmit()
    }
  },
  mounted(){
    this.onSubmit()
    this.allxue()
  }
};
</script>
<style lang="scss" >
.professional1 {
  .professional-inp {
     width: 100%;
    overflow: hidden;
    
  .pppp{
      width: 12.5%;
      float: left;
      margin: 0;
      margin-right: 20px;
      margin-top: 7px;
    }
    .el-form-item{
      margin-left: 20px;
    }
    .professional-btn{
      margin-top: 6px;
      float: right;
    }
  }
  .el-select{
    display: -webkit-box;
  }
  .ssss{
    float: left;
    margin-left: 38px;
    margin-top: 12px;
    font-size: 14px;
    line-height: 45px;
    font-weight: bold;
    color: #696969;
  }
  .ssssss{
      float: left;
      margin-top: 11px;
      font-size: 14px;
      line-height: 45px;
      font-weight: bold;
      color: #696969;
    }
  .block {
     float: left;
    width: 100%;
    text-align: right;
    padding-bottom: 12px;
    background-color: #fff;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.078);
  }
}
</style>



